.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: auto;
  max-width: 100%;
  min-width: 240px;
  padding: 40px;
  padding-bottom: 10px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center; /* Ensure the title stays centered */
}

.App-title {
  font-size: 1.75em;
  text-align: center;
  padding-left: .5%;
  
}

.App-intro {
  font-size: large;
}


.user-image {
  position: absolute;
  top: 0.25%;
  left: 7%;
  width: 15vw;
  max-width: 160px;
  min-width: 100px;
  height: 15vw; /* Make height equal to width */
  max-height: 160px;
  min-height: 100px;
  -o-object-fit: cover;
     object-fit: cover; /* Ensures the image fills the container */
  border-radius: 50%;
  -webkit-box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 10;
}


.ai-Tic-Tac-Toe {
  max-Width: "100px";
  border-Radius: "50%";
  margin-Bottom: "10px";
  height: 100px;

}

.format-text {
  text-align: justify; /* Align text evenly on both sides */
  margin-top: 10px;   /* Add spacing above the container */
}

.format-text p {
  margin-bottom: 10px; /* Add spacing between paragraphs */
  line-height: 1.6;    /* Increase line height for readability */
  text-indent: 20px;   /* Indent the first line of each paragraph */

}




@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



